.cinema-schedule{display:flex;flex-direction:column;width:300px;height:600px;overflow:hidden;border-radius:20px;box-shadow:3px 3px 10px rgba(0,0,0,.293),inset -3px -3px 10px rgba(0,0,0,.293)}.cinema-schedule__header{background-color:#000;margin:0;color:#fff;font-size:20px;padding:10px}.cinema-schedule__item{border-top:solid 1px #000;border-bottom:solid 1px #000;position:relative;width:100%;flex:1;transition:.7s ease}.cinema-schedule__item:hover{flex:1.5}.cinema-schedule__item::after{content:"Ви встигаєте на цей сеанс";color:#fff;justify-content:center;padding:20px;box-sizing:border-box;display:none;position:absolute;width:100%;height:100%;background-color:rgba(0,80,0,.499);left:0}.cinema-schedule__item--active::after{display:flex}.cinema-schedule__item-time{position:absolute;z-index:10;left:50%;bottom:5%;transform:translateX(-50%);background-color:rgba(0,0,0,.686);border-radius:20px;color:#fff;padding:5px 10px}.cinema-schedule__item-poster{left:0;top:0;position:absolute;width:100%;height:100%;object-fit:cover}